import Vue from 'vue'
import Router from 'vue-router'
import country from '@/components/country.vue'
import home from '@/components/home.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: home
    },
    {
      path: '/:country',
      name: 'Country',
      component: country
    }
  ]
})



